import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import "./index.scss";
import App from "./components/app";
import PerfectScrollbar from "react-perfect-scrollbar";

// Components
import Dashboard from "./components/dashboard";

//React Redux Components
import { Provider } from "react-redux";
import generateStore from "./store/store";

// Products physical
import VariablesProducts from "./components/products/physical/Productvariables";
import ProducType from "./components/products/physical/ProductType";
import Category from "./components/products/physical/category";
import Sub_category from "./components/products/physical/sub-category";
import Sub_category_dos from "./components/products/physical/sub-category-dos";
import Product_list from "./components/products/physical/product-list";
import ListarProductos from "./components/products/physical/ListarProductos";
import ListarFacturas from "./components/products/physical/ListarFacturas";
import ListarAnuncios from "./components/products/physical/ListarAnuncios";
import Add_product from "./components/products/physical/add-product";
import Product_detail from "./components/products/physical/product-detail";

//Product Digital
import Digital_category from "./components/products/digital/digital-category";
import Digital_sub_category from "./components/products/digital/digital-sub-category";
import Digital_pro_list from "./components/products/digital/digital-pro-list";
import Digital_add_pro from "./components/products/digital/digital-add-pro";

//Product Siigo
import AddProductSiigo from "./components/products/siigo/add-product-siigo";
import AddClientSiigo from "./components/products/siigo/add-cliente-siigo";

//Sales
import Orders from "./components/sales/orders";
import Transactions_sales from "./components/sales/transactions-sales";
import ValidateThird from "./components/sales/ValidateThird";
import CreateInvoice from "./components/sales/CreateInvoice";
import UpdateCodigosPrd from "./components/sales/UpdateCodigosPrd";
import UpdateSinCodigoSiigo from "./components/sales/UpdateSinCodigoSiigo";
import SaveInvoiceSiigo from "./components/sales/SaveInvoiceSiigo";

//Coupons
import ListCoupons from "./components/coupons/list-coupons";
import Create_coupons from "./components/coupons/create-coupons";

//Pages
import ListPages from "./components/pages/list-page";
import Create_page from "./components/pages/create-page";
import Media from "./components/media/media";
import List_menu from "./components/menus/list-menu";
import Create_menu from "./components/menus/create-menu";
import List_user from "./components/users/list-user";
import Create_user from "./components/users/create-user";

//Clientes
import List_customers from "./components/customers/list-customers";
import Interlocutor from "./components/customers/Interlocutor";
import CreateClientSiigo from "./components/customers/CreateClientSiigo";
import CreateInterlocutor from "./components/customers/CreateInterlocutor";

import Translations from "./components/localization/translations";
import Rates from "./components/localization/rates";
import Taxes from "./components/localization/taxes";
import Profile from "./components/settings/profile";
import Reports from "./components/reports/report";
import Invoice from "./components/invoice";
import Datatable from "./components/common/datatable";
import Login from "./components/auth/login";

import Usuarios from "./components/Usuarios/Usuarios";

//Firebase
import firebase from "./utilities/firebase";
import { getAuth, onAuthStateChanged, signOut } from "firebase/auth";

const store = generateStore();

const Root = () => {

	const [user, setUser] = useState(false);

	// Lee de la base de datos los datos de las paginas para navegar desde el menu inicial
	useEffect(() => {
		async function usuariologueado(dat) {
			//Valida si el Usuario esta logueado en Mercado Repuesto
			const auth = getAuth(firebase);
			onAuthStateChanged(auth, (user) => {
				if (user) {
					const uid = user.uid;
					console.log("USUARIO LOGUEADO : ", user.metadata.createdAt);
					setUser(true);
				} else {
					console.log("USUARIO NO ESTA LOGUEADO : ");
				}
			});
		}
		usuariologueado(0);
	}, []);

	return (
		<BrowserRouter basename={"/"}>
			<PerfectScrollbar>
				<Switch>
					<Route exact path={`${process.env.PUBLIC_URL}/`} component={Login} />
					<Route
						exact
						path={`${process.env.PUBLIC_URL}/auth/login`}
						component={Login}
					/>

					<App>
						<Route
							path={`${process.env.PUBLIC_URL}/dashboard`}
							component={Dashboard}
						/>

						<Route
							path={`${process.env.PUBLIC_URL}/users/usuarios`}
							component={Usuarios}
						/>

						<Route
							path={`${process.env.PUBLIC_URL}/products/physical/category`}
							component={Category}
						/>

						<Route
							path={`${process.env.PUBLIC_URL}/products/physical/ProductType`}
							component={ProducType}
						/>

						<Route
							path={`${process.env.PUBLIC_URL}/products/physical/Productvariables`}
							component={VariablesProducts}
						/>

						<Route
							path={`${process.env.PUBLIC_URL}/products/physical/sub-category`}
							component={Sub_category}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/physical/sub-category-dos`}
							component={Sub_category_dos}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/physical/product-list`}
							component={Product_list}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/physical/ListarProductos`}
							component={ListarProductos}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/physical/ListarFacturas`}
							component={ListarFacturas}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/physical/ListarAnuncios`}
							component={ListarAnuncios}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/physical/product-detail`}
							component={Product_detail}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/siigo/add-product-siigo`}
							component={AddProductSiigo}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/siigo/add-cliente-siigo`}
							component={AddClientSiigo}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/physical/add-product`}
							component={Add_product}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/digital/digital-category`}
							component={Digital_category}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/digital/digital-sub-category`}
							component={Digital_sub_category}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/digital/digital-product-list`}
							component={Digital_pro_list}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/products/digital/digital-add-product`}
							component={Digital_add_pro}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/sales/orders`}
							component={Orders}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/sales/transactions`}
							component={Transactions_sales}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/sales/CreateInvoice`}
							component={CreateInvoice}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/sales/UpdateCodigosPrd`}
							component={UpdateCodigosPrd}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/sales/UpdateSinCodigoSiigo`}
							component={UpdateSinCodigoSiigo}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/sales/SaveInvoiceSiigo`}
							component={SaveInvoiceSiigo}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/sales/ValidateThird`}
							component={ValidateThird}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/coupons/list-coupons`}
							component={ListCoupons}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/coupons/create-coupons`}
							component={Create_coupons}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/pages/list-page`}
							component={ListPages}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/pages/create-page`}
							component={Create_page}
						/>
						<Route path={`${process.env.PUBLIC_URL}/media`} component={Media} />

						<Route
							path={`${process.env.PUBLIC_URL}/menus/list-menu`}
							component={List_menu}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/menus/create-menu`}
							component={Create_menu}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/users/list-user`}
							component={List_user}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/users/create-user`}
							component={Create_user}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/customers/list-customers`}
							component={List_customers}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/customers/Interlocutor`}
							component={Interlocutor}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/customers/CreateClientSiigo`}
							component={CreateClientSiigo}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/customers/CreateInterlocutor`}
							component={CreateInterlocutor}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/localization/transactions`}
							component={Translations}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/localization/currency-rates`}
							component={Rates}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/localization/taxes`}
							component={Taxes}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/reports/report`}
							component={Reports}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/settings/profile`}
							component={Profile}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/invoice`}
							component={Invoice}
						/>
						<Route
							path={`${process.env.PUBLIC_URL}/data-table`}
							component={Datatable}
						/>
					</App>
				</Switch>
			</PerfectScrollbar>
		</BrowserRouter>
	);
};

ReactDOM.render(
	<Provider store={store}>
		<Root />
	</Provider>
	, document.getElementById("root"));

